import styles from "./register-page.module.css";
import React, { ChangeEvent, FormEvent, useRef, useState } from "react";
import clsx from "clsx";
import {
    Button,
    Input,
    PasswordInput,
} from "@ya.praktikum/react-developer-burger-ui-components";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import {UserRegisterDto} from "../../types/slice-types";


interface RegisterPageProps {
    onRegister: (userData: UserRegisterDto) => void;
}

const RegisterPage = ({ onRegister }: RegisterPageProps) => {
    const [userData, setUserData] = useState<UserRegisterDto>({
        email: "",
        password: "",
        name: "",
    });
    const inputRef = useRef<HTMLInputElement>(null);

    const onChange = (e: ChangeEvent<HTMLInputElement>) => {
        setUserData({ ...userData, [e.target.name]: e.target.value });
    };

    const onIconClick = () => {
        setTimeout(() => inputRef.current?.focus(), 0);
        alert("Icon Click Callback");
    };

    const handleSubmit = (e: FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        onRegister(userData);
    };

    return (
        <>
            <section className={clsx(styles.wrapper)}>
                <h1 className={clsx("text", "text_type_main-medium")}></h1>
                <form className={clsx(styles.form)} onSubmit={handleSubmit}>
                    <Input
                        type={"text"}
                        placeholder={""}
                        onChange={onChange}
                        icon={"CurrencyIcon"}
                        value={userData.name}
                        name={"name"}
                        error={false}
                        ref={inputRef}
                        onIconClick={onIconClick}
                        errorText={""}
                        size={"default"}
                        extraClass="ml-1"
                    />
                    <Input
                        type={"text"}
                        placeholder={"E-mail"}
                        onChange={onChange}
                        icon={"CurrencyIcon"}
                        value={userData.email}
                        name={"email"}
                        error={false}
                        ref={inputRef}
                        onIconClick={onIconClick}
                        errorText={""}
                        size={"default"}
                        extraClass="ml-1"
                    />
                    <PasswordInput
                        placeholder={""}
                        onChange={onChange}
                        value={userData.password}
                        name={"password"}
                        icon="EditIcon"
                    />
                    <Button htmlType="submit" type="primary" size="medium">
                          
                    </Button>
                </form>
                <ul className={clsx(styles.list)}>
                    <li className={clsx(styles.item)}>
                        <p
                            className={clsx(
                                "text text_type_main-default text_color_inactive"
                            )}
                        >
                             ?
                        </p>
                        <Link
                            to="/login"
                            className={clsx(styles.btn, "text text_type_main-default")}
                        >
                            
                        </Link>
                    </li>
                </ul>
            </section>
        </>
    );
};

RegisterPage.propTypes = {
    onRegister: PropTypes.func.isRequired,
};

export default RegisterPage;
